'use strict';

/* eslint-disable no-inner-declarations */
/* eslint-disable no-unused-vars */
// import * as flsFunction from './modules/functions';

// flsFunction.isWebp();

import search from './search.js';
import catalogMobile from './catalog.js';
import buildSlider from './slider.js';
import callGoods from './goods.js';
import openModal from './modal.js';
import chooseTown from './town.js';
import cartRender from './cart.js';
import buildHamburger from './hamburger.js';
import mobileMenu from './menu.js';
import topPage from './button-top.js';
import buildTabs from './tabs.js';
import sliderGoods from './slider-good.js';

try {
  search();
  catalogMobile();
  buildSlider();
  callGoods();
  openModal();
} catch (e) {
  // console.log(e);
} finally {
  chooseTown();
  cartRender();
  buildHamburger();
  mobileMenu();
  topPage();

  // Accordion
  const accord = document.getElementsByClassName('footer__accordion');
  let j;

  // eslint-disable-next-line no-plusplus
  for (j = 0; j < accord.length; j++) {
    accord[j].addEventListener('click', (event) => {
      event.target.classList.toggle('active');
      const panel = event.target.nextElementSibling;
      if (panel.style.maxHeight) {
        panel.style.maxHeight = null;
      } else {
        panel.style.maxHeight = `${panel.scrollHeight}px`;
      }
    });
  }
}

buildTabs();
sliderGoods();
