function buildSlider() {
// Слайдер

  // eslint-disable-next-line no-undef
  const swiper = new Swiper('.swiper', {
    // Optional parameters
    direction: 'horizontal',
    loop: true,
    autoplay: true,
    speed: 300,
    spaceBetween: 25,
    autoHeight: false,
    slidesPerView: 3.3,

    // If we need pagination
    pagination: {
      el: '.swiper-pagination',
    },

    // Navigation arrows
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },

    // And if we need scrollbar
    scrollbar: {
      el: '.swiper-scrollbar',
    },
    breakpoints: {
    // when window width is >= 320px
      280: {
        slidesPerView: 1.3,
      },
      400: {
        slidesPerView: 1.7,
      },
      577: {
        slidesPerView: 2.2,
      },
      769: {
        slidesPerView: 2.1,
      },
      // when window width is >= 640px
      993: {
        slidesPerView: 2.6,
      },
      1200: {
        slidesPerView: 3.3,
      },
    },
  });
}

export default buildSlider;
